import React from 'react';
import { useInView } from 'react-intersection-observer'; //           
import './Promo.css';
import MainContainer from '../../components/MainContainer/MainContainer';
import promoPhotoLeft from '../../images/main__promo_position_left.jpg';
import promoPhotoRight from '../../images/main__promo_position_right.jpg';

const Promo = () => {
  const [isActiveAnimation, setIsActiveAnimation] = React.useState(false); //  ?

  const { ref, inView } = useInView({
    threshold: 1,
  });

  React.useEffect(() => {
    setIsActiveAnimation(inView); //  ,      ,  
  }, [inView]);

  return (
    <MainContainer theme='additional'>
      <section className='promo'>
        <div className='promo__photo-container' ref={ref}>
          <img
            className={
              `promo__photo promo__photo_position_left ${isActiveAnimation ? 'promo__photo_animation_to-left' : ''}`
            }
            src={promoPhotoLeft}
            alt=' '
          />
          <img
            className={
              `promo__photo promo__photo_position_right ${isActiveAnimation ? 'promo__photo_animation_to-right' : ''}`
            }
            src={promoPhotoRight}
            alt=' '
          />
        </div>
        <div className='promo__text-container'>
          <h2 className='section-title'> </h2>
          <p className='promo__text'>      </p>
          <p className='promo__text'>
                         

          </p>
          <p className='promo__text'>      - . ,   </p>
        </div>
      </section>
    </MainContainer>
  );
};

export default Promo;
