import React from 'react';
import './ShelterVacancies.css';
import VacancyCard from '../../components/VacancyCard/VacancyCard';

const ShelterVacancies = () => {
  const [vacanciesList, setVacanciesList] = React.useState([]); //  

  React.useEffect(() => {
    setVacanciesList([ //    
      {
        id: 1,
        title: ' ',
        salary: '35 000   ',
        workSchedule: '',
        charge: '   ',
      },
      {
        id: 2,
        title: ' ',
        salary: '35 000   ',
        workSchedule: '',
        charge: '   ',
      },
      {
        id: 3,
        title: ' ',
        salary: '35 000   ',
        workSchedule: '',
        charge: '   ',
      },
    ]);
  }, []);

  return (
    <section className='shelter-section shelter-vacancies'>
      <h2 className='shelter-section__title'> </h2>
      <ul className='vacancies-list'>
        {vacanciesList.map((card) => {
          return (
            <VacancyCard
              id={card.id}
              title={card.title}
              salary={card.salary}
              workSchedule={card.workSchedule}
              charge={card.charge}
            />
          );
        })}
      </ul>
    </section>
  );
};

export default ShelterVacancies;
