import React from 'react';
import './MainPage.css';
import Banner from '../../modules/Banner/Banner';
import Promo from '../../modules/Promo/Promo';
import SheltersOnMain from '../../modules/SheltersOnMain/SheltersOnMain';
import PapersOnMain from '../../modules/PapersOnMain/PapersOnMain';
import Faq from '../../modules/Faq/Faq';
import mainApi from './api';

const MainPage = ({ loggedIn }) => {
  const [sheltersList, setSheltersList] = React.useState([]); //     
  const [papersList, setPapersList] = React.useState([]); //      
  const [faqList, setFaqList] = React.useState([]); //    

  React.useEffect(() => {
    mainApi
      .getSheltersOnMain(3) //       
      .then((shelters) => {
        setSheltersList(shelters.results);
      })
      .catch((err) => {
        console.log(err);
      });

    mainApi
      .getPapersOnMain(3) //        
      .then((papers) => {
        setPapersList(papers.results);
      })
      .catch((err) => {
        console.log(err);
      });

    mainApi
      .getFaq(4) //      
      .then((faqs) => {
        setFaqList(faqs.results);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <main className='main'>
      <Banner />
      <Promo />
      <SheltersOnMain loggedIn={loggedIn} sheltersList={sheltersList} />
      <PapersOnMain papersList={papersList} />
      <Faq faqList={faqList} />
    </main>
  );
};

export default MainPage;
