import React from 'react';
import './NewsPage.css';
import MainContainer from '../../components/MainContainer/MainContainer';
import NewsSection from '../../components/NewsSection/NewsSection';
import Button from '../../ui/Button/Button';
import newPhoto from '../../images/new-big-card.jpg';
import newPhoto1 from '../../images/new-card__photo_1.jpg';
import newPhoto2 from '../../images/new-card__photo_2.jpg';
import newPhoto3 from '../../images/new-card__photo_3.jpg';

const NewsPage = () => {
  const [newsList, setNewsList] = React.useState([]); //  

  React.useEffect(() => {
    setNewsList([ //    
      {
        id: 1,
        title: '    ',
        data: '12.12.2022',
        shelter: ' ',
        description: `          
        .            `,
        mainPhoto: newPhoto,
      },
      {
        id: 2,
        title: '    ',
        data: '12.12.2022',
        shelter: ' ',
        description: `          
        .            `,
        mainPhoto: newPhoto1,
      },
      {
        id: 3,
        title: '    ',
        data: '12.12.2022',
        shelter: ' ',
        description: `          
        .            `,
        mainPhoto: newPhoto2,
      },
      {
        id: 4,
        title: '    ',
        data: '12.12.2022',
        shelter: ' ',
        description: `          
        .            `,
        mainPhoto: newPhoto3,
      },
    ]);
  }, []);

  return (
    <MainContainer theme='base'>
      <main className='main news'>
        <div className='news__title-row'>
          <h2 className='news__title'></h2>
          <Button className='margin-left_auto' onClick={() => {}} disabled> </Button>
        </div>
        <NewsSection newsList={newsList} />
      </main>
    </MainContainer>
  );
};

export default NewsPage;
