import React from 'react';
import { useNavigate } from 'react-router-dom'; //     
import { useInView } from 'react-intersection-observer'; //  ,      
import './SheltersOnMain.css';
import MainContainer from '../../components/MainContainer/MainContainer';
import ShelterCard from '../../components/ShelterCard/ShelterCard';
import Button from '../../ui/Button/Button';

const SheltersOnMain = ({ loggedIn, sheltersList }) => {
  const [isActiveAnimation, setIsActiveAnimation] = React.useState(false); //  ?
  const [isOpenPopup, setIsOpenPopup] = React.useState(false); //      ?

  const navigate = useNavigate(); //    

  const { ref, inView } = useInView({
    threshold: 1, //   ,      100%
  });

  const handleAddShelterButton = () => {
    // eslint-disable-next-line no-unused-expressions
    loggedIn ? navigate('/profile') : setIsOpenPopup(true);
  };

  React.useEffect(() => {
    setIsActiveAnimation(inView); //  ,      ,  
  }, [inView]);

  return (
    // eslint-disable-next-line react/jsx-no-comment-textnodes
    <MainContainer theme='base'>
      {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
      <section
        className='shelters-on-main'
        onClick={() => {
          // eslint-disable-next-line no-unused-expressions
          isOpenPopup && setIsOpenPopup(false);
        }}
      >
        <h2 className='section-title'>,    </h2>
        <ul className='shelters-on-main__shelters-container'>
          {sheltersList.map((shelter) => {
            return (
              <li key={shelter.id}>
                <ShelterCard
                  id={shelter.id}
                  name={shelter.name}
                  address={shelter.address}
                  workingHours={shelter.working_hours}
                  logo={shelter.logo}
                  profileImage={shelter.profile_image}
                />
              </li>
            );
          })}
        </ul>
        <Button className='shelters-on-main__map-button' to='/shelters' link>  </Button>
        <div
          className={
            `shelters-on-main__text-container ${isActiveAnimation ? 'shelters-on-main__text-container_animation' : ''}`
          }
          ref={ref}
        >
          <h3 className='shelters-on-main__text-title'>  ?</h3>
          <p className='shelters-on-main__text-subtitle'>        !</p>
          <Button className={` ${!loggedIn && 'button_disabled'}`} onClick={handleAddShelterButton}> </Button>
          <div className={`shelters-on-main__popup ${isOpenPopup && 'shelters-on-main__popup_opened'}`}>
            <p className='shelters-on-main__popup-text'>       </p>
            <Button className='margin_column-center' to='/sign-up' link></Button>
          </div>
        </div>
      </section>
    </MainContainer>
  );
};

export default SheltersOnMain;
