import React from 'react';
import './Banner.css';
import MainContainer from '../../components/MainContainer/MainContainer';
import Button from '../../ui/Button/Button';
import bannerImage from '../../images/main__banner.png';

const Banner = () => {
  return (
    <MainContainer theme='base'>
      <section className='banner'>
        <img className='banner__image' src={bannerImage} alt='' />
        <h1 className='banner__title'> ,    </h1>
        <div className='banner__line' />
        <p className='banner__subtitle'>  -      .</p>
        <Button className='banner__button' to='/shelters' link> </Button>
      </section>
    </MainContainer>
  );
};

export default Banner;
